import React, { useState, useRef } from 'react';
import {NavLink} from 'react-router-dom';
import './Sidebar.css'
import { useAuth, currentUser } from '../../contexts/AuthContext'
import {
    FaHome,
    FaGlasses,
    FaBars,
    FaBug,
    FaPeopleArrows,
    FaUniversity,
    FaSignOutAlt
} from 'react-icons/fa';
import { BiMessageAltDetail } from "react-icons/bi";
import { BsEmojiSunglasses } from "react-icons/bs";
import { MdCorporateFare } from "react-icons/md";



const Sidebar = ({children}) => {

    const { currentUser } = useAuth()
    const LoginADM = 'kvoltareli@gmail.com'
    const VerificaADM = currentUser.email

    const[isOpen, setIsOpen] = useState(false);
    const toggle = () => setIsOpen (!isOpen);

    if(VerificaADM !== LoginADM) {
        var menuItem = [            

            {
                path:"/home",  
                name:"Home",
                icon: <FaHome/>    
            },
            {
                path:"/menucatalogo",  
                name:"Catalogo",
                icon: <FaGlasses/>        
            },
            {
                path:"/Outlet",  
                name:"Outlet",
                icon: <BsEmojiSunglasses/>        
            },
            {
                path:"/avisos",  
                name:"Avisos",
                icon: <BiMessageAltDetail/>        
            },
            {
                path:"/treinamentos",  
                name:"Treinamento",
                icon: <FaUniversity/>        
            },
            {
                path:"/Valores",  
                name:"Código de conduta",
                icon: <MdCorporateFare/>        
            },
            {
                path:"/contatodev",  
                name:"Reportar bug",
                icon: <FaBug/>        
            },
            {
                path:"/*",  
                name:'Logout',
                icon: <FaSignOutAlt/>        
            }        
        ]
    } else {
        var menuItem = [            

            {
                path:"/home",  
                name:"Home",
                icon: <FaHome/>    
            },
            {
                path:"/menucatalogo",  
                name:"Catalogo",
                icon: <FaGlasses/>        
            },
            {
                path:"/Outlet",  
                name:"Outlet",
                icon: <BsEmojiSunglasses/>        
            },
            {
                path:"/avisos",  
                name:"Avisos",
                icon: <BiMessageAltDetail/>        
            },            
            {
                path:"/treinamentos",  
                name:"Treinamento",
                icon: <FaUniversity/>        
            },
            {
                path:"/Valores",  
                name:"Código de conduta",
                icon: <MdCorporateFare/>        
            },
            {
                path:"/7d2abf2d0fa7c3a0c13236910f30bc43",  
                name:'Cadastrar funcionário',
                icon: <FaPeopleArrows/>        
            },
            {
                path:"/contatodev",  
                name:"Reportar bug",
                icon: <FaBug/>        
            },
            {
                path:"/*",  
                name:'Logout',
                icon: <FaSignOutAlt/>        
            }        
        ]
    }
  
    

    return (
        <div className='barratoggle'>            
            <div style={{width:isOpen ? "230px" : "50px"}} className='sidebar'>
                <div className='top_selection'>                  
                
                <h1 style={{display:isOpen ? "block" : "none"}} className='logo'>                       
                Menu                          
                </h1>

                    <div style={{marginLeft:isOpen ? "auto" : "0"}} className='bars'>
                        <FaBars 
                        className='fabars'
                        onClick={toggle} />
                    </div>
                </div>
                {
                    menuItem.map((item,index) => (
                        <NavLink 
                            to={item.path} 
                            key={index} 
                            className="link" 
                            activeclassName="active"
                        >
                            <div className="icon">{item.icon}</div>
                            <div style={{display:isOpen ? "block" : "none"}} className="link_text">{item.name}</div>
                        </NavLink>
                    ))
                }
                
            </div>
            <div>
                
            </div>
            <main>{children}</main>
        </div>
    );
};

export default Sidebar;