import React from 'react'
import Sidebar from './Sidebar'
import './Valores.css'
import ModalValores from '../ModalValores'
import ModalPrincipios from '../ModalPrincipios'
import ModalPrincipiosClientes from '../ModalPrincipiosCliente'
import ModalPosturaConcorrencia from '../ModalPosturaConcorrencia'
import ModalDispGeral from '../ModalDispGeral'
import { Grid } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'



function Valores() {
  return (
    <Sidebar>
        <div className='ContainerValores'>
          <Grid columns={4} divided>
            <Grid.Row>
              <Grid.Column style={{margin:'10px'}} >
                <ModalValores />
              </Grid.Column>
              <Grid.Column style={{margin:'10px'}}>
                <ModalPrincipios />
              </Grid.Column>
              <Grid.Column style={{margin:'10px'}}>
                <ModalPrincipiosClientes />
              </Grid.Column>
              <Grid.Column style={{margin:'10px'}}>
                <ModalPosturaConcorrencia />
              </Grid.Column>
              <Grid.Column style={{margin:'10px'}}>
                <ModalDispGeral />
              </Grid.Column>
            </Grid.Row>   
          </Grid>      
        </div>
    </Sidebar>
  )
}

export default Valores
