import React from 'react'
import { Link } from 'react-router-dom'
import { Card, Image } from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css';
import Sidebar from '../pages/Sidebar';
import styles from './MenuCatalogo.module.css'

const MenuCatalogo = () => (
<Sidebar>
<div className={styles.ContainerMenuCatalogo}>
  <div style={{height:'200px', width: '200px'}} className={styles.ContainerCardMenu}>
  <Card>
    <Image style={{height:'200px'}} src='/IMAGE/circus_SGP.jpg' wrapped ui={false} />
    <Card.Content>
      <Card.Header>Santo Grau Prime</Card.Header>
      
    </Card.Content>
    <Card.Content extra>
      <a>
        <Link to="/cardSGP">Clique aqui para acessar</Link>
      </a>
    </Card.Content>
  </Card>
  </div>

  <div style={{height:'200px', width: '200px'}}  className={styles.ContainerCardMenu}>
  <Card>
    <Image style={{height:'200px'}} src='/IMAGE/maya_clipon.jpg' wrapped ui={false} />
    <Card.Content>
      <Card.Header>Clipon</Card.Header>
      
    </Card.Content>
    <Card.Content extra>
      <a>
        <Link to="/cardclipon">Clique aqui para acessar</Link>
      </a>
    </Card.Content>
  </Card>
  </div>

<div style={{height:'200px', width: '200px'}}  className={styles.ContainerCardMenu}>
  <Card>
    <Image style={{height:'200px'}} src='/IMAGE/dualipa_drip.jpg' wrapped ui={false} />
    <Card.Content>
      <Card.Header>Drip</Card.Header>
      
    </Card.Content>
    <Card.Content extra>
      <a>
        <Link to="/carddrip">Clique aqui para acessar</Link>
      </a>
    </Card.Content>
  </Card>
  </div>

<div style={{height:'200px', width: '200px'}}  className={styles.ContainerCardMenu}>
  <Card>
    <Image style={{height:'200px'}} src='/IMAGE/label_sunglass.jpg' wrapped ui={false} />
    <Card.Content>
      <Card.Header>Sunglass</Card.Header>
      
    </Card.Content>
    <Card.Content extra>
      <a>
        <Link to="/cardsunglass">Clique aqui para acessar</Link>
      </a>
    </Card.Content>
  </Card>
</div>

<div style={{height:'200px', width: '200px'}}  className={styles.ContainerCardMenu}>
  <Card>
        <Image style={{height:'200px'}} src='/IMAGE/caixinha.jpg' wrapped ui={false} />
    <Card.Content>
      <Card.Header>Todas grifes</Card.Header>
      
    </Card.Content>
    <Card.Content extra>
      <a>
        <Link to="/catalogo">Clique aqui para acessar</Link>
      </a>
    </Card.Content>
  </Card>
</div>
</div>

  </Sidebar>  
)

export default MenuCatalogo
