import React from 'react';
import BarraLateral from './Sidebar';
import SlideshowHome from './SliderHome';
import 'semantic-ui-css/semantic.min.css';
import styles from './Home.module.css'

function Home() {
  
 

  return (
    <BarraLateral>
      <div className={styles.ContainerHome}>
        <div>
          <div className={styles.GridAvisosImportante}>
              <h1>Titulo aviso importante</h1>
              <p>Texto aviso importante</p>
              <h6><strong>@Nome autor, Data aviso</strong></h6>
          </div>

          <div className={styles.Grid2}>
              <h1>Links uteis</h1>
              <p>Texto links uteis</p>
              <h6><strong>@Nome autor, Data aviso</strong></h6>
          </div>
        </div>
        <div className={styles.GridSlide}>
          <SlideshowHome/>
        </div>
      </div>
    </BarraLateral>
  )  
  }

export default Home