import React from 'react'
import { useEffect, useState } from 'react';
import './Avisos.css'
import Sidebar from './Sidebar'
import CriarPost from './CriarPosts';
import { useAuth, currentUser } from '../../contexts/AuthContext'

export default function Avisos() {

  //logica para liberar botão
    const { currentUser } = useAuth()
    const LoginADM = 'kvoltareli@gmail.com'
    const VerificaADM = currentUser.email

    var ADMlogado = false;

    if(LoginADM === VerificaADM){
      var ADMlogado = true;
    }
  
  //conexão com api GET -> Post page
    const [data, setData] = useState ([]);
    const getProdutos = async() => {
      fetch("http://192.168.30.252:9091/app_portal_server/backend/visualiza_post.php")
      .then((response) => response.json())
      .then((responseJson) => (
        //console.log(responseJson),
        setData(responseJson.records)
      ));
    }
  
    useEffect(() => {
      getProdutos();
    }, [])


  return (
    <Sidebar> 
    {ADMlogado ? <CriarPost/> : ''}
    

    <div className='avisos'>
    {Object.values(data).map((val, key) => {
        return <div className='post'>
            <div className='postHeader'>
                <div className='title'><h1>{val.TITULO_POST}</h1></div>                 
            </div>      
                         
            <div className='postTextContainer'>
                {val.POST}
            </div>
            <div className='postHeader'>
                <div className='tituloAutor'><h6>@{val.AUTOR_POST}</h6></div>
                <div className='dataContainer'>

              <div className='postHeader'>
                  <div className='title'><h6>{val.DATA_POST}</h6></div>
              </div>
            </div>
            </div>
            
        </div>
      })}
    </div>
    </Sidebar>
  )
}
